.dashboard-table.ReactTable {
	max-height: 100%;
	background: white;
	border: 1px solid  rgba(92, 92, 92, 0.6);
	border-radius: 6px;
	box-shadow: 0 0 8px rgba(92, 92, 92, 0.5);
	margin-bottom: 15px;
	font-family: "Open Sans Condensed";
	font-weight: 800;
	-webkit-animation: slide-in-right 0.7s;
	        animation: slide-in-right 0.7s;
	width: 100%;
	max-width: calc(100% - 42px);
	float: left;
}

.dashboard-table.ReactTable .rt-noData {
	color: rgba(0, 0, 0, 0.75);
}

.dashboard-table .rt-th {
	color: darkslategrey;
}

.dashboard-table .rt-thead {
	overflow-y: scroll;
	overflow-x: hidden;
}

.dashboard-table .rt-thead::-webkit-scrollbar {
	background: transparent;
}

.dashboard-table .rt-tbody {
	overflow-x: hidden !important;
}

.dashboard-table .rt-td {
	font-weight: 400;
}

/**
 * ----------------------------------------
 * animation slide-in-right
 * ----------------------------------------
 */
@-webkit-keyframes slide-in-right {
	0% {
		-webkit-transform: translateX(1000px);
		transform: translateX(1000px);
		opacity: 0;
	}

	100% {
		-webkit-transform: translateX(0);
		transform: translateX(0);
		opacity: 1;
	}
}

@keyframes slide-in-right {
	0% {
		-webkit-transform: translateX(1000px);
		transform: translateX(1000px);
		opacity: 0;
	}

	100% {
		-webkit-transform: translateX(0);
		transform: translateX(0);
		opacity: 1;
	}
}

.input-range__slider {
	-webkit-appearance: none;
	        appearance: none;
	background: #3f51b5;
	border: 1px solid #3f51b5;
	border-radius: 100%;
	cursor: pointer;
	display: block;
	height: 1rem;
	margin-left: -0.5rem;
	margin-top: -0.65rem;
	outline: none;
	position: absolute;
	top: 50%;
	transition: box-shadow 0.3s ease-out, -webkit-transform 0.3s ease-out;
	transition: transform 0.3s ease-out, box-shadow 0.3s ease-out;
	transition: transform 0.3s ease-out, box-shadow 0.3s ease-out, -webkit-transform 0.3s ease-out;
	width: 1rem;
}

.input-range__slider:active {
	-webkit-transform: scale(1.3);
	        transform: scale(1.3);
}

.input-range__slider:focus {
	box-shadow: 0 0 0 5px rgba(63, 81, 181, 0.2);
}

.input-range--disabled .input-range__slider {
	background: #ccc;
	border: 1px solid #ccc;
	box-shadow: none;
	-webkit-transform: none;
	        transform: none;
}

.input-range__slider-container {
	transition: left 0.3s ease-out;
}

.input-range__label {
	color: #2d2d2d;
	font-family: Arial;
	font-weight: bold;
	font-size: 0.6rem;
	-webkit-transform: translateZ(0);
	        transform: translateZ(0);
	white-space: nowrap;
}

.input-range__label--min,
.input-range__label--max {
	display: none;
}

.input-range__label--min {
	left: 0;
}

.input-range__label--max {
	right: 0;
}

.input-range__label--value {
	position: absolute;
	top: -1.4rem;
}

.input-range__label-container {
	left: -50%;
	position: relative;
}

.input-range__label--max .input-range__label-container {
	left: 50%;
}

.input-range__track {
	background: #eee;
	border-radius: 0.3rem;
	cursor: pointer;
	display: block;
	height: 0.3rem;
	position: relative;
	transition: left 0.3s ease-out, width 0.3s ease-out;
}

.input-range--disabled .input-range__track {
	background: #eee;
}

.input-range__track--background {
	left: 0;
	margin-top: -0.15rem;
	position: absolute;
	right: 0;
	top: 50%;
}

.input-range__track--active {
	background: #3f51b5;
}

.input-range {
	height: 1rem;
	position: relative;
	width: 100%;
}

